import Inputmask from 'inputmask';
import '../src/styles/main.scss';
import Swiper from 'swiper';
import 'swiper/css';

const initPopularSwiper = () => {
    new Swiper('.sectionPopular__swiper', {
        loop: false,
        slidesPerView: 'auto',
        spaceBetween: 20,
        centeredSlides: false,
        navigation: {
            nextEl: '.sectionPopular__button--next',
            prevEl: '.sectionPopular__button--prev',
            disabledClass: 'sectionPopular__button--disabled'
        },
        breakpoints: {
            768: {
                slidesPerView: 2,
                spaceBetween: 30
            },
            1024: {
                slidesPerView: 3,
                spaceBetween: 40
            }
        }
    });
};

document.addEventListener('DOMContentLoaded', initPopularSwiper);


const phoneInputs = document.querySelectorAll('.phone-mask');
const im = new Inputmask('+7 (999) 999-99-99');
im.mask(phoneInputs);

document.querySelector('.contact-form').addEventListener('submit', function(e) {
    e.preventDefault();
    const form = e.target;
    const successMessage = form.querySelector('.success-message');
    successMessage.classList.add('visible');
    //     setTimeout
    setTimeout(function() {
      successMessage.style.display = 'flex';
      form.reset();
      
      setTimeout(function() {
        successMessage.style.display = 'none';
        successMessage.classList.remove('visible');
      }, 3000);
    }, 500);
  });

  ymaps.ready(init);
    
    function init() {
        //  
        const map = new ymaps.Map('map', {
            center: [44.611245, 40.108364], //  
            zoom: 15,
            controls: ['zoomControl']
        });

        //  
        const marker = new ymaps.Placemark([44.611245, 40.108364], {
            hintContent: ' ',
            balloonContent: '. , . , 5'
        }, {
            preset: 'islands#redDotIcon'
        });

        map.geoObjects.add(marker);
    }

    //    API
document.addEventListener('DOMContentLoaded', () => {
    //    ymaps
    if (typeof ymaps !== 'undefined') {
        ymaps.ready(initMap);
    } else {
        console.error('Yandex Maps API  !');
    }
});

document.addEventListener('DOMContentLoaded', () => {
    if (typeof ymaps !== 'undefined') {
        ymaps.ready(initMap);
    } else {
        console.error('Yandex Maps API  !');
    }
});

function initMap() {
    const coordinates = [44.611245, 40.108364];

    //  
    const map = new ymaps.Map('yandex-map', {
        center: coordinates,
        zoom: 16,
        controls: ['zoomControl', 'fullscreenControl']
    });

    //  
    const marker = new ymaps.Placemark(coordinates, {
        balloonContent: '. , . , 5',
        hintContent: '  '
    }, {
        preset: 'islands#redIcon',
        iconColor: '#e74c3c'
    });

    //    
    map.geoObjects.add(marker);
    
    //   
    marker.balloon.open();
}